import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'PolygonDex',
  description:
    'The most popular AMM on BSC by user count! Earn PODEX through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by PolygonDex), NFTs, and more, on a platform you can trust.',
  image: 'https://app.polygondex.finance/logo.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | ${t('PolygonDex')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('PolygonDex')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('PolygonDex')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('PolygonDex')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('PolygonDex')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('PolygonDex')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('PolygonDex')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('PolygonDex')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('PolygonDex')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('PolygonDex')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('PolygonDex')}`,
      }
    default:
      return null
  }
}
